<h1 id="android-broadcast-receivers" tabindex="-1"><a class="header-anchor" href="#android-broadcast-receivers" aria-hidden="true">#</a> Android Broadcast Receivers</h1>
<p>Every now and then Android broadcasts different type of intents with information, such as Internet connection availability, Bluetooth enabled/disabled, etc. Applications can register and listen for these broadcasts using broadcast receivers, and if they want they can even broadcast their own intents, which other applications can listen for.</p>
